@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
    color-scheme: dark;
}

body {
    background-color: #000;
    color: #fff;
    font-family: 'Inter', system-ui, -apple-system, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* Custom Scrollbar */
::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}

::-webkit-scrollbar-track {
    background: transparent; 
}

::-webkit-scrollbar-thumb {
    background: #334155; 
    border-radius: 9999px;
}

::-webkit-scrollbar-thumb:hover {
    background: #4b5563; 
}

::selection {
    background: #8b5cf6;
    color: white;
}

/* Global Utils */
@layer components {
    .glass-panel {
        @apply bg-gray-900/80 backdrop-blur-md border border-white/10 shadow-xl;
    }
    
    .animated-gradient-bg {
        background: linear-gradient(90deg, #ff0080, #7928ca, #ff0080);
        background-size: 200% 200%;
        animation: gradient-move 3s ease infinite;
    }

    .rainbow-text {
        @apply bg-clip-text text-transparent bg-gradient-to-r from-pink-500 via-red-500 via-yellow-500 via-green-500 via-blue-500 to-purple-500;
        background-size: 200% auto;
        animation: shine 5s linear infinite;
    }

    .rainbow-border-bottom {
        @apply border-b-2 border-transparent;
        border-image: linear-gradient(to right, #ec4899, #ef4444, #eab308, #22c55e, #3b82f6, #a855f7) 1;
    }
}

@keyframes gradient-move {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
}

@keyframes shine {
    to {
        background-position: 200% center;
    }
}
